import All from "../ActionConst";
import produce from "immer";

const State = {
  login: false,
  usingHeader: true,

  content: [],
  followContent: [],
  hotContent: [],
};

const ReducerX = (prestate = State, action) => {
  switch (action.type) {
    case "Fetch-New-Content":
      return produce(prestate, draftState => {
        draftState.content = mokeData(info); //first fetch
      });
    case "Fetch-New-FollowContent":
      return produce(prestate, draftState => {
        draftState.followContent = mokeData(followInfo);
      });
    case "Fetch-New-HotContent":
      return produce(prestate, draftState => {
        draftState.hotContent = mokeData(hotInfo);
      });
    default:
      return prestate;
  }
};
export default ReducerX;

//template for content
let info = {
  title: "?",
  to: "/home",
  content: `start:-1 -2 end: start `,
  like: "9990",
  comment: "9999",
  type: "answer",
  imgsrc: "",
};

let followInfo = {
  ...info,
  followed: "vzch",
  time: "10 years ago",
  author: {
    avatar: "",
    usrname: "",
    usrtag: "1",
    usrdesc: "XX, XX",
  },
};

let hotInfo = {
  ...info,
  hot: 111,
};

const mokeData = info => {
  let conf = [];
  for (let i = 0; i < 10; i++) {
    conf.push(info);
  }

  return conf;
};
